import React from 'react';
import './App.scss';
import Header from "./components/Header/Header";
import Main from "./components/Main/Main";
//import Footer from "./components/Footer/Footer";
import { BrowserRouter as Router } from 'react-router-dom';

function App() {
  return (
    <div className="App">
        <Router>
            <Header />
            <Main />
        </Router>

    </div>
  );
}

export default App;
